	module.exports = {"data":{"nodePost":{"title":"To PESOS or to POSSE - Dries Buytaert","body":{"value":"<blockquote>\r\n<p>The <a href=\"https://indieweb.org\">IndieWeb movement</a> has provided two clever names for these models:\r\n</p>\r\n<ol>\r\n <li><strong>PESOS</strong> or <a href=\"https://indieweb.org/PESOS\">Publish Elsewhere, Syndicate (to your) Own Site</a> is a model where publishing begins on third party services, such as Facebook, and then copies can be syndicated to your own site.</li>\r\n<li><strong>POSSE</strong> or <a href=\"https://indieweb.org/POSSE\">Publish (on your) Own Site, Syndicate Elsewhere</a> is a publishing model that begins with posting content on your own site first, then syndicating out copies to third party services.</li>\r\n</ol>\r\n</blockquote>\r\n<p>I've been thinking about this as well as I build out this website. I fall into the POSSE camp, and right now it's completely manual and mostly just me posting links on Twitter. To some extent, I'm even fine with this website just being its own disconnected thing. It will always be a small subset of my social network that would make it here and actually read something, and that seems just about right.</p>"},"relationships":{"image":null},"created":1518367028}},"pathContext":{"slug":"/link/to-pesos-or-to-posse-dries-buytaert/"}}

